import UniLoadMore from '@/components/UniLoadMore/UniLoadMore'
export default {
	components:{
		UniLoadMore,
	},
	data(){
		return {
			noData:'more',
			page:1,
			listRows:10,
			dataList:[],
		}
	},
	onShow(){
		this.page = 1;
		this.listRows = 10;
		this.dataList = [];
		this.reqStoreList();
	},
	onReachBottom(){
		if(this.noData === 'more'){
			this.reqStoreList()
		}else{
			return false
		}
	},
	methods:{
		reqStoreList(){
			this.noData = 'loading';
			this.$request.GET({
				url:this.$api.apiUrl.GET_V6_BRAND_LISTING,
				data:{
					page:this.page,
					listRows:this.listRows
				}
			}).then(res=>{
				if(res.code === 'SUCCESS'){
					if(res.data.length < 10){
						this.noData = 'noMore';
					}else{
						// 如果还有数据就让page+1
						this.page++;
						this.noData = 'more';
					}
					this.dataList = this.dataList.concat(res.data);
				}else{
					this.$api.msg(res.msg)
				}
			})
		},
		submit(e){
			// 按的是什么按钮
			var listId = e.target.dataset.btn;
			// 审核列表的id
		    var parentId = e.target.dataset.id;
			this.$request.POST({
				url:this.$api.apiUrl.POST_V6_BRAND_CHECK,
				data:{
					"id": parentId,
					"status": listId
				}
			}).then(res=>{
				if(res.code == 'SUCCESS'){
					let index = this.dataList.findIndex(item=>{
						return item.id == parentId
					})
					// 更改显示页面的
					this.$set(this.dataList[index],'status',listId)
				}else{
					this.$api.msg(res.msg)
				}
			})
		}
	}
}
